%special-picture {
  background-size: cover;
}
.special-bottom-div {
  height: 20%;
  display: flex;
}
.special-background-1 {
  @extend %special-picture;
  height: 80%;
  background-image: url("https://cdn.shortpixel.ai/client/q_glossy,ret_img,w_730/https://krakowski-centus.pl/wp-content/uploads/2020/01/ile_mog%C4%99_oszcz%C4%99dzi%C4%87-730x485.jpg");
}
.special-background-2 {
  @extend %special-picture;
  height: 100%;
  background-image: url("https://ak.picdn.net/shutterstock/videos/1654414/thumb/10.jpg");
}
%special-bottom-text {
  height: 100%;
  display: flex;
}
.special-text-left {
  width: 65%;
  background: #e6e6e6;
  font-size: 1.5vw;
  justify-content: center;
  align-items: center;
  vertical-align: baseline;
  @extend %special-bottom-text;
}
.special-text-right {
  width: 35%;
  background: #f0f0f0;
  flex-direction: column;
  justify-content: center;
  @extend %special-bottom-text;
}

%p-max100 {
  width: 90%;
  margin: 0;
  text-align: end;
}
.special-p-main {
  @extend %p-max100;
  font-size: 1vw;
  font-weight: 600;
}
.special-p-desc {
  @extend %p-max100;
  font-size: 0.8vw;
  font-weight: 400;
}
.special-text-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  color: white;
}
.special-text-main {
  display: flex;
  flex-direction: column;
  text-align: center;
}
.special-text-main > p {
  margin-bottom: 0.2vw;
  font-size: 2vw;
}
.special-text-main > span {
  font-size: 1vw;
}
.special-text-button {
  border: 2px solid white;
  border-radius: 5%;

}
.special-text-button > p {
  margin: 5px 15px;
}

.special-offerts {
  background-color: red;
  display: flex;
  flex-wrap: wrap;
}
.special-add-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 80%;
  margin: auto;
}
.special-add-objects {
  width: 48%;
  height: 300px;
}
@media screen and (max-width: 800px) {
  .special-add-container{
    padding-top:10px ;
    width: 100%;
   
  }
  .special-add-objects {
    width: 100%;
    height: 150px;
    margin-bottom: 5px;;
  }
}
