.shopping-details
{
    width: 100%;
    height: 200px;
    margin-top: 20px;
    margin-bottom: 20px;
    background: rgba(19, 46, 113, 1);
    display: flex;
    justify-content: center;
}
.circle-info
{
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-self: center;
}
.info-details{
    width: 100px;
    height: 100px;
    // background: pink;
    border-radius: 50%;
    background-image: url("../Pictures/deliveryOnBoard.png");
    margin-top: 5%;
    margin-bottom: 5%;
}
.shopping-info-details{
    width: 23%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    /* align-self: center; */
    align-items: center;
    color:white;
}
.shopping-info-details > p
{
    font-size: 0.9em;
    font-weight: 700;
}
